import comHeader from '@/components/header/header'
import Debounce from '@/util/debounce'
import { getContact, getAll } from '@/api/certification'

export default {
  name: 'contents',
  components: {
    comHeader
  },
  data () {
    return {
      relationList: ['朋友', '同事', '配偶', '直系亲属'],
      relationship: '同事',
      name: '',
      phone: '',
      relationship1: '同事',
      name1: '',
      phone1: ''
    }
  },
  beforeMount () {
    this._onData()
  },
  methods: {
    async _onData () {
      getAll().then(({ data: { code, data, msg } }) => {
        if (code === 1 && data && data.userContacts) {
          this.relationship = data.userContacts.relationship
          this.name = data.userContacts.name
          this.phone = data.userContacts.phone
          this.relationship1 = data.userContacts.relationship1
          this.name1 = data.userContacts.name1
          this.phone1 = data.userContacts.phone1
        }
      }).catch(() => {
        this.$vux.toast.text('网络出错，请稍后重试')
      })
    },
    handleSave () {
      const rules = {
        name: {
          name: "紧急联系人1姓名",
          type: "validName",
          value: this.name,
          required: !0
        },
        phone: {
          name: "紧急联系人1手机号",
          type: "validMobile",
          value: this.phone,
          required: !0
        },
        name1: {
          name: "紧急联系人2姓名",
          type: "validName",
          value: this.name1,
          required: !0
        },
        phone1: {
          name: "紧急联系人2手机号",
          type: "validMobile",
          value: this.phone1,
          required: !0
        }
      }
      if (!this.$utils.dataValidityCheck(rules)) return
      Debounce(() => this._onSave(), 1000)
    },
    async _onSave () {
      let params = {
        relationship: this.relationship,
        name: this.name,
        phone: this.phone,
        relationship1: this.relationship1,
        name1: this.name1,
        phone1: this.phone1
      }
      getContact(params).then(({ data: { code, data, msg } }) => {
        if (code === 1) {
          this.$vux.toast.text('保存成功')
          this.$router.back(-1)
        }
      }).catch(() => {
        this.$vux.toast.text('网络出错，请稍后重试')
      })
    }
  }
}



